@import url(https://fonts.googleapis.com/css?family=Creepster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Mystery+Quest&display=swap);
@import url(https://fonts.googleapis.com/css?family=Eater&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  background-image: url(https://wallpapercave.com/wp/ZUILYly.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 1000px;
}

.header {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 0 auto;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#ff2400),
    color-stop(#e81d1d),
    color-stop(#e8b71d),
    color-stop(#e3e81d),
    color-stop(#1de840),
    color-stop(#1ddde8),
    color-stop(#2b1de8),
    color-stop(#dd00f3),
    to(#dd00f3)
  );
  background: -webkit-linear-gradient(
    left,
    #ff2400,
    #e81d1d,
    #e8b71d,
    #e3e81d,
    #1de840,
    #1ddde8,
    #2b1de8,
    #dd00f3,
    #dd00f3
  );
  background: linear-gradient(
    to right,
    #ff2400,
    #e81d1d,
    #e8b71d,
    #e3e81d,
    #1de840,
    #1ddde8,
    #2b1de8,
    #dd00f3,
    #dd00f3
  );
}

.header button {
  color: white;
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  height: 40px;
  width: 10em;
  margin: 2em;
  border: 3px solid white;
  border-radius: 6px;
  background: -webkit-linear-gradient(
    326deg,
    #ff2400,
    #e81d1d,
    #e8b71d,
    #e3e81d,
    #1de840,
    #1ddde8,
    #2b1de8,
    #dd00f3,
    #dd00f3
  );
  background: linear-gradient(
    124deg,
    #ff2400,
    #e81d1d,
    #e8b71d,
    #e3e81d,
    #1de840,
    #1ddde8,
    #2b1de8,
    #dd00f3,
    #dd00f3
  );
  background-size: 1800% 1800%;
  -webkit-animation: rainbow 18s ease infinite;
          animation: rainbow 18s ease infinite;
  cursor: pointer;
}

@-webkit-keyframes rainbow {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}

@keyframes rainbow {
  0% {
    background-position: 0% 82%;
  }
  50% {
    background-position: 100% 19%;
  }
  100% {
    background-position: 0% 82%;
  }
}

button:focus {
  outline: 0;
}

.day {
  background: rgba(253, 106, 2, 0.7);
  border-radius: 10px;
  width: 50%;
  margin: 40px auto;
}

h1 {
  font-family: "Creepster", cursive;
  font-size: 3.5em;
  text-align: center;
  color: #51ff0d;
  padding-top: 20px;
}

.fa-hotdog {
  color: #1ddde8;
  margin-left: 2em;
}

label {
  font-family: "Mystery Quest", cursive;
  margin: 0 0.5em;
  font-size: 2em;
  color: #2b1de8;
}

.buttons {
  border: none;
  cursor: pointer;
  background: none;
}

.buttons:hover {
  opacity: 0.5;
}

.fa-trash-alt {
  color: #2b1de8;
}

.fa-edit {
  color: #7416ed;
}

.fa-calendar-plus {
  color: #dd00f3;
}

.editInput {
  margin: 15px 0.5em 15px 1em;
  font-size: 1.5em;
  padding-left: 0.5em;
  color: #7416ed;
}

.fa-check-square {
  color: #7416ed;
  font-size: 1.5em;
}

.dayOption {
  margin-left: 2em;
  margin-top: 0.5em;
}

.dayLabel {
  font-family: "Eater", cursive;
  margin-left: 0.5em;
  color: #dd00f3;
}

.fa-calendar-check {
  color: #dd00f3;
  margin-left: 9em;
  margin-top: 0.5em;
  margin-bottom: 1em;
  font-size: 1.5em;
}

.input {
  margin: 15px 0.5em 15px 1em;
  font-size: 1.5em;
  padding-left: 0.5em;
}

.input:focus {
  outline: 0;
}

.fa-plus {
  height: 1.5em;
  font-size: 1.5em;
  color: #51ff0d;
}

